import React from 'react';
import PropTypes from 'prop-types';
import { formatAsCurrency } from '@aib/uikit-react/dist/Form/CurrencyInput/currencyFormatter';
import LoadingSkeleton from '@aib/uikit-react/dist/Animations/LoadingSkeleton/LoadingSkeleton';
import * as S from './FinancialDetails.styled';

function FinancialDetails({
  purchasePrice,
  deposit,
  repaymentAmount,
  isLoadingSkeletonShown,
}) {
  const financialDetails = [
    { label: 'Purchase Price', amount: purchasePrice },
    { label: "Deposit you'll need", amount: deposit },
    { label: 'Monthly repayment', amount: repaymentAmount },
  ];

  return financialDetails.map((financialDetail, i) => (
    <S.FinancialItemContainer key={i}>
      <S.FinancialItemLabel>{financialDetail.label}:</S.FinancialItemLabel>
      <LoadingSkeleton isVisible={isLoadingSkeletonShown}>
        <S.FinancialItemAmount>
          {formatAsCurrency(String(financialDetail.amount), '€') || '--'}
        </S.FinancialItemAmount>
      </LoadingSkeleton>
    </S.FinancialItemContainer>
  ));
}

FinancialDetails.propTypes = {
  purchasePrice: PropTypes.number.isRequired,
  deposit: PropTypes.number.isRequired,
  repaymentAmount: PropTypes.number.isRequired,
  isLoadingSkeletonShown: PropTypes.bool.isRequired,
};

export default FinancialDetails;
