import React, { useState } from 'react';
import PropTypes from 'prop-types';
import * as TS from '../../templates/Template.styled';
import MainQuestion from '../../Form/MainQuestion/MainQuestion';
import ProceedButton from '../../Form/ProceedButton/ProceedButton';
import LoanAmountConnected from '../../BuildingBlocks/LoanAmount/LoanAmountConnected';
import ContactMethods from '../../BuildingBlocks/ContactMethods/ContactMethods';
import FinancialDetails from '../../BuildingBlocks/FinancialDetails/FinancialDetails';
import OutsidePolicyConnected from '../../BuildingBlocks/OutsidePolicy/OutsidePolicyConnected';
import Range from '@aib/uikit-react/dist/Form/Range/Range';
import Tabs from '@aib/uikit-react/dist/Navigation/Tabs/Tabs';
import Link from '@aib/uikit-react/dist/Navigation/Link/Link';
import IconInfo from '@aib/uikit-react/dist/Icons/IconInfo';
import AlertPopup from '@aib/uikit-react/dist/Content/AlertPopup/AlertPopup';
import Button from '@aib/uikit-react/dist/Form/Button/Button';
import LoadingSkeleton from '@aib/uikit-react/dist/Animations/LoadingSkeleton/LoadingSkeleton';
import { color } from '@aib/design-tokens/dist/design-tokens';
import * as S from './ResultsStep.styled';

const tabTitles = {
  F: 'family home',
  O: '1 bed apartment',
};

function ResultsStep({
  termData,
  selectedTermIndex = 0,
  rangeData,
  onChangeRange,
  onChangeTab,
  isAibMobileApp = false,
  isOutsidePolicy,
  isCriticalError,
  isPostCalculationInProgress,
  isPostSaveInProgress,
  selectedLoanPurpose,
  onClickContinue,
  onClickCriticalErrorAlert,
  onClickCallUs,
  onClickBackToAccounts,
}) {
  const [hasSmallerHeaderText, setHasSmallerHeaderText] = useState(false);

  const handleTabChange = tabIndex => {
    const loanPurposeId = Object.keys(tabTitles)[tabIndex];
    onChangeTab(loanPurposeId);
  };

  const handleProceedClick = () => {
    onClickContinue();
  };

  const handleClickBackToAccounts = () => {
    onClickBackToAccounts();
  };

  const handleClickCallUs = () => {
    onClickCallUs();
  };

  const onShowMaxLoanAmount = isMaxLoanAmountShown => {
    setHasSmallerHeaderText(isMaxLoanAmountShown);
  };

  const handleTermChange = value => {
    onChangeRange(parseInt(value));
  };

  const handleCriticalErrorAlertClick = () => {
    onClickCriticalErrorAlert();
  };

  const isLoadingSkeletonShown =
    isPostCalculationInProgress || isPostSaveInProgress;

  return (
    <S.ResultsContainer>
      {!isOutsidePolicy && (
        <React.Fragment>
          <AlertPopup
            title="Sorry..."
            isOpen={isCriticalError}
            onConfirm={handleCriticalErrorAlertClick}
            confirmLabel="Close"
          >
            We&apos;re having trouble with our site. Please come back later.
          </AlertPopup>
          <S.ResultsHeader>
            <MainQuestion
              hasSmallerText={hasSmallerHeaderText}
              centered
              questionText="Based on the information you've given us, the maximum amount we can lend you is:"
            />
          </S.ResultsHeader>
          <TS.QuestionContent>
            <LoanAmountConnected
              amount={termData.approvedLoanAmount}
              onShowMaxLoanAmount={onShowMaxLoanAmount}
              isLoadingSkeletonShown={isLoadingSkeletonShown}
            />
            <S.TabsContainer>
              <LoadingSkeleton isVisible={isLoadingSkeletonShown}>
                <Tabs
                  tabTitles={Object.values(tabTitles)}
                  onTabChange={index => handleTabChange(index)}
                  defaultSelectedTab={Object.keys(tabTitles).indexOf(
                    selectedLoanPurpose
                  )}
                />
              </LoadingSkeleton>
            </S.TabsContainer>
            <FinancialDetails
              purchasePrice={termData.approvedPurchasePrice}
              deposit={termData.depositAmount}
              repaymentAmount={termData.repaymentAmount}
              isLoadingSkeletonShown={isLoadingSkeletonShown}
            />
            <S.StandoutFrame>
              <S.MainText>Adjust your mortgage term:</S.MainText>
              <LoadingSkeleton isVisible={isLoadingSkeletonShown}>
                <Range
                  step="1"
                  min={String(rangeData.indexOf(rangeData[0]))}
                  max={String(
                    rangeData.indexOf(rangeData[rangeData.length - 1])
                  )}
                  initialValue={String(selectedTermIndex)}
                  onChange={handleTermChange}
                  formatLabel={value =>
                    value !== undefined && rangeData[value] + ' years'
                  }
                  testId="termRange"
                />
              </LoadingSkeleton>
            </S.StandoutFrame>

            <S.Paragraph>
              <LoadingSkeleton isVisible={isLoadingSkeletonShown}>
                <div>
                  These monthly repayments are based on a Loan to Value (LTV) of
                  <strong> {termData.loanToValue}%</strong>, an interest rate of{' '}
                  <strong>{termData.interestRate}% </strong>
                  and Annual Percentage Rate (APR) of{' '}
                  <strong>{termData.annualisedPercentageRate}% </strong> over
                  the term shown.
                </div>
              </LoadingSkeleton>
            </S.Paragraph>

            <S.Paragraph>
              We used an interest rate that is variable. We also have low fixed
              rates that you can choose from when you apply.
            </S.Paragraph>

            <S.StandoutFrame>
              <S.InfoSection>
                <S.InfoIcon>
                  <IconInfo color={color.darkGreen100} />
                </S.InfoIcon>
                <S.InfoLink>
                  <Link
                    openInNewTab
                    href={
                      'https://aib.ie/our-products/mortgages/mortgage-interest-rate-options'
                    }
                    size="large"
                  >
                    More info about our fixed and variable rates
                  </Link>
                </S.InfoLink>
              </S.InfoSection>
            </S.StandoutFrame>
          </TS.QuestionContent>

          {isAibMobileApp
            ? mobileCtas(handleClickBackToAccounts, handleClickCallUs)
            : ctas(handleProceedClick, isLoadingSkeletonShown)}
        </React.Fragment>
      )}
      {!!isOutsidePolicy && <OutsidePolicyConnected />}
    </S.ResultsContainer>
  );
}

function ctas(handleProceedClick, isContinueButtonDisabled) {
  return (
    <>
      <S.Paragraph>
        To generate your Approval in Principle (AIP) document over this amount
        and term, we need to gather more information about you.
      </S.Paragraph>
      <TS.QuestionFooter>
        <TS.QuestionFooterButton>
          <ProceedButton
            onClick={handleProceedClick}
            isDisabled={isContinueButtonDisabled}
            buttonLabel="I want my Approval in Principle"
          />
        </TS.QuestionFooterButton>
      </TS.QuestionFooter>
      <S.Paragraph>
        Approval in Principle is only a guide amount based on limited
        information. It doesn&apos;t consider all of your financial
        circumstances. It&apos;s not an offer of a mortgage loan but you can use
        it to go house hunting.
      </S.Paragraph>
      <S.Paragraph>
        <Link
          openInNewTab
          href={
            ' https://aib.ie/our-products/mortgages/Home-Mortgages-Regulatory-Information'
          }
          size="medium"
        >
          Home Mortgages Regulatory Information
        </Link>
      </S.Paragraph>
      <S.Paragraph />

      <ContactMethods />
    </>
  );
}

function mobileCtas(handleClickBackToAccounts, handleClickCallUs) {
  return (
    <>
      <TS.QuestionFooter>
        <TS.QuestionFooterButton>
          <Button onClick={handleClickCallUs} label="Call us" fullWidth />
        </TS.QuestionFooterButton>
        <TS.QuestionFooterButton>
          <Button
            buttonStyle="secondary"
            onClick={handleClickBackToAccounts}
            label="Back to accounts"
            fullWidth
          />
        </TS.QuestionFooterButton>
      </TS.QuestionFooter>
      <S.Paragraph>
        If you want, you can apply online for a mortgage by visiting our website
        aib.ie/our-products/mortgages or drop into any AIB branch where we can
        discuss the best option for you.
      </S.Paragraph>
      <S.Paragraph>
        <Link
          openInNewTab
          href={
            ' https://aib.ie/our-products/mortgages/Home-Mortgages-Regulatory-Information'
          }
          size="medium"
        >
          Home Mortgages Regulatory Information
        </Link>
      </S.Paragraph>
    </>
  );
}

ResultsStep.propTypes = {
  termData: PropTypes.object.isRequired,
  selectedTermIndex: PropTypes.number,
  rangeData: PropTypes.array.isRequired,
  onChangeRange: PropTypes.func.isRequired,
  onChangeTab: PropTypes.func.isRequired,
  isAibMobileApp: PropTypes.bool,
  isOutsidePolicy: PropTypes.bool.isRequired,
  isCriticalError: PropTypes.bool.isRequired,
  isPostCalculationInProgress: PropTypes.bool.isRequired,
  isPostSaveInProgress: PropTypes.bool.isRequired,
  selectedLoanPurpose: PropTypes.string.isRequired,
  onClickContinue: PropTypes.func.isRequired,
  onClickCriticalErrorAlert: PropTypes.func.isRequired,
  onClickCallUs: PropTypes.func.isRequired,
  onClickBackToAccounts: PropTypes.func.isRequired,
};

export default ResultsStep;
