import PropTypes from 'prop-types';
import React, { useEffect } from 'react';
import Toolbar from './Navigation/Toolbar/Toolbar';
import packageJson from '../../package.json';
import StepConnected from './Steps/Step/StepConnected';
import DebugToolbar from './Debug/DebugToolbar';
import PageBody from './Layout/PageBody/PageBody';
import ProgressBarContainer from './Layout/ProgressBar/ProgressBarContainer';
import { stepsArray } from './Steps/Steps.list';
import { setHostEventListener } from '../store/middleware/hostMiddleware';

import { setFetchImplementation, setApiBaseUrl } from '../api/restApi';

// Log the Calculator Version to the Browser Console
console.log('@aib/mortgage-calculator package.version', packageJson.version);

function Calculator(props) {
  const {
    onClickBack,
    currentStep,
    currentStepNumber,
    hostStepNumber,
    apiBaseUrl = '',
    isCalcNavBarVisible = true,
    isAibMobileApp = false,
    isDebugToolbarVisible = false,
    fetchImplementation,
    gotoStepNumber,
    onChangeStepNumber,
    eventListener,
    onClickClose,
  } = props;

  useEffect(() => {
    setFetchImplementation(fetchImplementation);
    apiBaseUrl && setApiBaseUrl(apiBaseUrl);
  }, [fetchImplementation, apiBaseUrl]);

  /* eslint-disable */
  useEffect(() => {
    console.log(
      'Calc: sending updated page number to host:',
      currentStepNumber
    );
    onChangeStepNumber && onChangeStepNumber(currentStepNumber);
  }, [currentStepNumber]);

  useEffect(() => {
    console.log('Calc: Received stepNumber from host:', hostStepNumber);
    if (hostStepNumber !== currentStepNumber) {
      gotoStepNumber(hostStepNumber);
    }
  }, [hostStepNumber]);

  useEffect(() => {
    console.log({ props });
    if (eventListener) {
      setHostEventListener(eventListener);
    }
  }, []);
  /* eslint-enable */

  function handleCloseClick() {
    onClickClose();
  }

  return (
    <div id="MortgageCalculator">
      {isDebugToolbarVisible && <DebugToolbar />}

      {isCalcNavBarVisible && (
        <Toolbar
          onClickBack={onClickBack}
          onClickClose={handleCloseClick}
          hasBackButton={currentStep !== stepsArray[0]}
          hasCloseButton={true}
          title="How much can I borrow?"
        />
      )}
      <PageBody>
        <ProgressBarContainer isNavBarVisible={isCalcNavBarVisible} />
        <StepConnected isAibMobileApp={isAibMobileApp} />
      </PageBody>
    </div>
  );
}

Calculator.propTypes = {
  apiBaseUrl: PropTypes.string,
  currentStep: PropTypes.string.isRequired,
  currentStepNumber: PropTypes.number.isRequired,
  hostStepNumber: PropTypes.number,
  fetchImplementation: PropTypes.func,
  onClickBack: PropTypes.func.isRequired,
  onClickClose: PropTypes.func.isRequired,
  eventListener: PropTypes.func,
  isCalcNavBarVisible: PropTypes.bool,
  isDebugToolbarVisible: PropTypes.bool,
  isAibMobileApp: PropTypes.bool,
  gotoStepNumber: PropTypes.func.isRequired,
  onChangeStepNumber: PropTypes.func,
};
export default Calculator;
