import { connect } from 'react-redux';
import {
  gotoPreviousStep,
  gotoStepByNumber,
} from '../store/questions/questions.actions';
import { appClose } from '../store/app/app.actions';
import {
  selectCurrentStep,
  selectCurrentStepNumber,
} from '../store/questions/questions.selectors';
import Calculator from './Calculator';

const mapDispatchToProps = (dispatch, ownProps) => ({
  onClickBack: () => dispatch(gotoPreviousStep()),
  gotoStepNumber: stepNumber => dispatch(gotoStepByNumber(stepNumber)),
  onClickClose: () => {
    if (ownProps.onCloseCalculator) {
      console.warn(
        'DEPRECATED: onCloseCalculator() replaced by eventListener()'
      );
      ownProps.onCloseCalculator('onCloseCalculator');
    }
    dispatch(appClose());
  },
});

const mapStateToProps = (state, ownProps) => {
  return {
    ...ownProps,
    currentStep: selectCurrentStep(state),
    currentStepNumber: selectCurrentStepNumber(state),
  };
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(Calculator);
